.main-hero__container-position {
  @extend .container;
  position: relative;
  z-index: 10;
  // margin-left: 20px;
}
.main-hero__header {
  font-family: 'Titan One';
  font-weight: 400;
  font-size: 26px;
  line-height: 1.15;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: $text-white;
  @include tab {
    font-size: 22px;
    line-height: 1.14;
  }
  @include desk {
    font-size: 38px;
    line-height: 1.16;
  }
}
.main-hero__header-accent {
  font-size: 48px;
  line-height: 0.96;
  color: $text-main;
  @include tab {
    font-size: 34px;
    line-height: 0.91;
  }
  @include desk {
    font-size: 58px;
    line-height: 0.9;
  }
}
.main-hero__list {
  padding-bottom: 158px;
  display: flex;
  margin-top: 20px;
  @include tab {
    padding-bottom: 0;
    margin-top: 7px;
    margin-bottom: 20px;
  }
  @include desk {
    margin-top: 24px;
    margin-bottom: 40px;
  }
}
.main-hero__btn-product {
  @extend %btn;
  padding: 11px 14px;
  font-weight: 400;
  font-size: 8px;
  line-height: 1.25px;
  margin-right: 8px;
  @include desk {
    font-size: 14px;
    line-height: 1.29px;
    padding: 19px 24px;
  }
}
.main-hero__btn-made {
  @extend %btn-reverse;
  font-weight: 400;
  font-size: 8px;
  line-height: 1.25px;
  padding: 11px 13px;
  @include desk {
    font-size: 14px;
    line-height: 1.29px;
    padding: 19px 20px;
  }
}
.main-hero__content {
  display: flex;
  align-items: center;
}
.main-hero__wrapper {
  display: none;
  @include tab {
    display: block;
    margin-left: auto;
    margin-right: 15px;
  }
}
.main-hero__wrapper-margin {
  margin-bottom: 16px;
}
.main-hero__text {
  position: relative;
  display: block;
  font-family: 'Titan One';
  font-weight: 400;
  font-size: 28px;
  line-height: 1.14;
  text-align: right;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: #d41443;
  @include desk {
    font-size: 46px;
    line-height: 1.15;
  }
  &::after {
    content: '';
    display: block;
    width: 16px;
    height: 16px;
    background-image: url(../images/hero-ellipse-tab-1x-min.png);
    background-image: -webkit-image-set(url(../images/hero-ellipse-tab-1x-min.webp) 1x);

    @media screen and (min-device-pixel-ratio: 2),
      screen and (-moz-min-device-pixel-ratio: 2),
      screen and (-o-min-device-pixel-ratio: 2/1),
      screen and (-webkit-min-device-pixel-ratio: 2),
      screen and (min-resolution: 192dpi),
      screen and (min-resolution: 2dppx) {
      background-image: url(../images/hero-ellipse-tab-2x-min.png);
      background-image: -webkit-image-set(url(../images/hero-ellipse-tab-2x-min.webp) 2x);
    }
    background-size: contain;
    background-repeat: no-repeat;
    position: absolute;
    top: -3px;
    right: -10px;
    // transform: translate(50%, 50%);
    z-index: -1;
    @include desk {
      width: 30px;
      height: 30px;
      top: -7px;
      right: -15px;
      background-image: url(../images/hero-ellipse-desctop-1x-min.png);
      background-image: -webkit-image-set(url(../images/hero-ellipse-desc-1x-min.webp) 1x);

      @media screen and (min-device-pixel-ratio: 2),
        screen and (-moz-min-device-pixel-ratio: 2),
        screen and (-o-min-device-pixel-ratio: 2/1),
        screen and (-webkit-min-device-pixel-ratio: 2),
        screen and (min-resolution: 192dpi),
        screen and (min-resolution: 2dppx) {
        background-image: url(../images/hero-ellipse-desctop-2x-min.png);
        background-image: -webkit-image-set(url(../images/hero-ellipse-desc-2x-min.webp) 2x);
      }
    }
  }
}
@media screen and (min-device-pixel-ratio: 2),
  screen and (-moz-min-device-pixel-ratio: 2),
  screen and (-o-min-device-pixel-ratio: 2 / 1) {
  .main-hero__text::after {
    background-image: url(../images/hero-ellipse-tab-2x-min.png);
    @include desk {
      background-image: url(../images/hero-ellipse-desctop-2x-min.png);
    }
  }
}
.main-hero__text-color {
  display: block;
  font-family: 'DM Sans';
  font-weight: 400;
  font-size: 8px;
  line-height: 1.25;
  color: #ffffff;
  @include desk {
    font-size: 14px;
    line-height: 1.29;
  }
}
.main-hero__title {
  display: none;
  @include tab {
    display: block;
    width: 114px;
    font-family: 'DM Sans';
    font-weight: 400;
    font-size: 8px;
    line-height: 1.5;
    color: #ffffff;
  }
  @include desk {
    width: 200px;
    font-size: 14px;
    line-height: 1.43;
  }
}
.main-hero__btn-modal {
  display: none;
  @include tab {
    margin-top: 3px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 18px;
    height: 18px;
    border: none;
    cursor: pointer;
    border-radius: 50%;
    background-color: #fff;
  }
  @include desk {
    width: 26px;
    height: 26px;
    margin-top: 19px;
  }
}
.main-hero__btn-icon {
  fill: $main-btn;
}
.main-hero__bottom {
  display: none;
  @include tab {
    display: flex;
    align-items: center;
    margin-top: 20px;
  }
}
.main-hero__milk {
  @include desk {
    // width: 213px;
    // height: 216px;
  }
}

.main-hero__girl {
  display: none;
  @include tab {
    display: block;
    position: absolute;
    bottom: -15px;
    right: 15px;
  }
  @include desk {
    // width: 304px;
    // height: 241px;
    bottom: -60px;
  }
}
@media screen and (max-width: 768px) {
  .main-hero__container {
    overflow: hidden;
  }
}
