.gallery {
    background-image: linear-gradient(to bottom, $bcg-light 40%, #fff 40%);
}

.gallery__grid {
    display: grid;
    grid-gap: 1px;
    grid-template-columns: repeat(7, 1fr);
    grid-template-rows: repeat(9, 1fr);

    @include tab {
        grid-gap: 4px;
        grid-template-columns: repeat(9, 1fr);
        grid-template-rows: repeat(6, 1fr);
    }

    @include desk {
        grid-gap: 6px;
    }
}

.gallery__item {
    border-radius: 5px;
    overflow: hidden;
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1), box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1), opacity 500ms cubic-bezier(0.4, 0, 0.2, 1);

    @include tab {
        border-radius: 15px;
    }

    @include desk {
        border-radius: 20px;
    }

    &:hover {
        transform: scale(1.2);
        box-shadow: 0px 1px 9px rgb(0 0 0 / 10%), 0px 1px 1px rgb(0 0 0 / 4%), 0px 2px 1px rgb(0 0 0 / 3%);
        z-index: 1;
    }

    &:hover:nth-child(4) {
        transform: scale(1.1);
    }

}

.gallery__item-1 {
    grid-column-start: 1;
    grid-column-end: 2;
    grid-row-start: 4;
    grid-row-end: 7;

    @include tab {
        grid-column-start: 2;
        grid-column-end: 3;
    }
}

