.header {
  width: 100%;
  background-color: $bcg-dark;
  padding-top: 8px;
  padding-bottom: 25px;

  @include desk {
    padding-top: 37px;
    padding-bottom: 60px;
  }

  @include tab {
    padding-top: 32px;
    padding-bottom: 20px;
  }
}

.header__logo {
  width: 199px;
  height: 50px;

  @media screen and (min-width: 768px) {
    margin-right: 293px;
  }

  @media screen and (min-width: 1280px) {
    margin-right: 0;
  }
}

.main-nav {
  display: flex;

  @media screen and (max-width: 767px) {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  @media screen and (min-width: 1200px) {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.site-nav {
  display: none;
}

.site-nav__item {
  color: $text-white;

  transition: color $anim-fast;
}

.site-nav__item:hover,
.site-nav__item:focus {
  color: $accent-hover;
}

@include desk {
  .site-nav {
    display: flex;

    font-size: 16px;
    line-height: 1.3;

    font-weight: 700;
  }
}

.site-nav__item:not(:last-child) {
  margin-right: 35px;
}

// .current {
//     color: $text-main;
// }

.header__button {
  display: none;
}

@include tab {
  .header__button {
    display: inline-block;

    font-family: 'DM Sans';

    border: none;
    cursor: pointer;
    color: $text-main;
    background-color: $secondary-btn;

    min-width: 159px;
    height: 44px;
    display: flex;
    align-items: center;
    text-align: center;
    border-radius: 22px;
    padding: 12px 37px 11px 37px;

    margin-right: 0;
    margin-left: 32px;

    font-size: 16px;
    line-height: 1.3;

    font-weight: 700;
  }

  .header__button--svg {
    margin-left: 12px;
  }
}

html {
  scroll-behavior: smooth;
}
